.submit-form-root  {
    display: grid;
    grid-template-columns: 30px auto 30px;
    grid-template-rows: 30px auto 30px auto 30px;
   
  }

  .upper{
    grid-column-start: 2;
    grid-column-end: 3;
    grid-row-start:2;
    grid-row-end:3;
    background-color: rgb(200, 200, 200);
    height: auto;
    margin-top: '20px';
    -webkit-box-shadow: 0px 1px 5px 0px grey;
            box-shadow: 0px 1px 5px 0px grey;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around
  }

  .upperLeft{
    width: 70%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column
  }

  .upperRight{
    width: 30%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -ms-flex-align: center;
        align-items: center
  }

  .lower{
    grid-column-start: 2;
    grid-column-end: 3;
    grid-row-start:4;
    grid-row-end:5;
    background-color: rgb(200, 200, 200);
    height: auto;
    margin-top: 20px;
    -webkit-box-shadow: 0px 1px 5px 0px grey;
            box-shadow: 0px 1px 5px 0px grey;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around
  }

  .lowerLeft {
    width: 50%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column
  }

  .lowerRight {
    width: 50%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: justify;
        justify-content: space-between
  }
  .submitButton {
    width: 80%
  }
  .addButton {
    margin-bottom: 20px
  }
  .cameraButton{
    width: 70px;
    height: 70px;
    margin-bottom: 8px
  }
  .cameraIcon {
    font-size: 50px
  }
  .input {
    display: none;
  }

